import { Component } from "react";
import { withRouter } from "react-router-dom";
import Carousel from "./Carousel";
import ErrorBoundary from "./ErrorBoundary";
import ThemeContext from "./ThemeContext";
import Modal from "./Modal";

class Details extends Component {
  state = { loading: true, showModal: false };

  onRequestClose = () => {
    this.toggleModal;
  };
  componentDidMount = async () => {
    const res = await fetch(
      `http://pets-v2.dev-apis.com/pets?id=${this.props.match.params.id}`
      // get id from App.js : line 22
      // < Route path = "/details/:id" >
    );
    const json = await res.json();
    this.setState(
      Object.assign(
        {
          loading: false,
        },
        json.pets[0]
      )
    );
    // this.setState({
    //   loading: false,
    //   breed: json.pets[0].breed,
    //   name: json.pets[0].name,
    //   animal: json.pets[0].animal,
    //   city: json.pets[0].city,
    //   state: json.pets[0].state
    //   description: json.pets[0].description
    // });
  };
  toggleModal = () => this.setState({ showModal: !this.state.showModal });

  adopt = () => (window.open("http://bit.ly/pet-adopt"), "_blank");
  render() {
    if (this.state.loading) {
      return <h2>loading...</h2>;
    }
    const { animal, breed, city, state, description, name, images, showModal } =
      this.state;

    return (
      <div className="details">
        <Carousel images={images} />
        <div>
          <h1>{name}</h1>
          <h2>
            {animal} - {breed} - {city}, {state}
          </h2>
          {/* <ThemeContext.Consumer /> only works in Class Component */}
          <ThemeContext.Consumer>
            {(
              // [theme]
              themehook
            ) => (
              <button
                onClick={this.toggleModal}
                style={{
                  backgroundColor:
                    // theme
                    themehook[0],
                }}
              >
                Adopt {name}
              </button>
            )}
            {/* {(themehook) => {themehook[0]}} */}
          </ThemeContext.Consumer>
          <p>{description}</p>
          {showModal ? (
            <Modal>
              <div>
                <h1>Would you like to adopt {name}?</h1>
                <div className="buttons">
                  <button onClick={this.adopt}>Yes</button>
                  <button onClick={this.toggleModal}>No</button>
                </div>
              </div>
            </Modal>
          ) : null}
        </div>
      </div>
    );
  }
}

// without ErrorBoundary
// export default withRouter(Details);

const DetailsWithRouter = withRouter(Details);

export default function DetailsWithErrorBoundary() {
  return (
    <ErrorBoundary>
      <DetailsWithRouter />
    </ErrorBoundary>
  );
}
